___CSS_LOADER_EXPORT___.push([module.id, `.button {
  background-color: transparent;
  border: 1px solid grey;
  border-radius: 0.7rem;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  height: 4rem;
  width: 4rem;
  transition: all 0.4s ease-in-out;
  overflow: hidden;
}

.button:hover {
  width: 13rem;
  box-shadow: 1px 1px 0 0 grey;
}

.button:active {
  box-shadow: 0 0 0 0 grey;
  transition: all 0.1s ease-in-out;
  transform: translate(2px, 2px);
}

.button__text-wrapper {
  overflow: hidden;
  opacity: 0;
  transition: all 0.4s ease-in-out;
}

.button__text {
  color: grey;
  transition: all 1s ease-in-out;
}

.button:hover .button__text-wrapper {
  opacity: 1;
  overflow: visible;
}

.button__image-wrapper {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 4rem;
  min-width: 4rem;
}

.button__plane-icon-img {
  position: absolute;
}

.button__done-icon-img {
  opacity: 0;
  position: absolute;
  width: 2rem;
  height: 2rem;
  transition: opacity 1s ease-in-out;
}

.button--fix-size {
  width: 13rem;
}

.plane--rotate {
  animation: move-plane 5s ease-in-out;
  transition: all 1s ease-in-out;
}

.opacity--zero {
  opacity: 0;
}

.opacity--visible {
  opacity: 1;
}

.button__text {
  margin: 0;
}

.button--shaking {
  animation: shake 0.2s linear;
  background-color: #ff5959;
}

.form--submit {
  background-color: #a8ffa8;
}

@keyframes move-plane {
  0% {
    rotate: 0;
    transform: translate(0, 0);
  }
  10% {
    rotate: 30deg;
    transform: translate(0, 0);
  }
  40% {
    transform: translate(193px, -116px);
  }
  50% {
    transform: translate(200px, 200px);
  }
  75% {
    transform: translate(-200px, 200px);
  }
  100% {
    transform: translate(0, 0);
  }
}
@keyframes shake {
  0% {
    transform: translate(1px, 1px) rotate(0deg);
  }
  10% {
    transform: translate(-1px, -2px) rotate(-1deg);
  }
  20% {
    transform: translate(-3px, 0px) rotate(1deg);
  }
  30% {
    transform: translate(3px, 2px) rotate(0deg);
  }
  40% {
    transform: translate(1px, -1px) rotate(1deg);
  }
  50% {
    transform: translate(-1px, 2px) rotate(-1deg);
  }
  60% {
    transform: translate(-3px, 1px) rotate(0deg);
  }
  70% {
    transform: translate(3px, 1px) rotate(-1deg);
  }
  80% {
    transform: translate(-1px, -1px) rotate(1deg);
  }
  90% {
    transform: translate(1px, 2px) rotate(0deg);
  }
  100% {
    transform: translate(1px, -2px) rotate(-1deg);
  }
}`, "",{"version":3,"sources":["webpack://./src/modules/send-button/_send-button.scss"],"names":[],"mappings":"AAAA;EACE,6BAAA;EACA,sBAAA;EACA,qBAAA;EACA,aAAA;EACA,2BAAA;EACA,mBAAA;EACA,YAAA;EACA,WAAA;EACA,gCAAA;EACA,gBAAA;AACF;;AAEA;EACE,YAAA;EACA,4BAAA;AACF;;AAEA;EACE,wBAAA;EACA,gCAAA;EACA,8BAAA;AACF;;AAEA;EACE,gBAAA;EACA,UAAA;EACA,gCAAA;AACF;;AAEA;EACE,WAAA;EACA,8BAAA;AACF;;AAEA;EACE,UAAA;EACA,iBAAA;AACF;;AAEA;EACE,kBAAA;EACA,aAAA;EACA,uBAAA;EACA,mBAAA;EACA,YAAA;EACA,eAAA;AACF;;AAEA;EACE,kBAAA;AACF;;AAEA;EACE,UAAA;EACA,kBAAA;EACA,WAAA;EACA,YAAA;EACA,kCAAA;AACF;;AAEA;EACE,YAAA;AACF;;AAEA;EACE,oCAAA;EACA,8BAAA;AACF;;AAGA;EACE,UAAA;AAAF;;AAGA;EACE,UAAA;AAAF;;AAGA;EACE,SAAA;AAAF;;AAGA;EACE,4BAAA;EACA,yBAAA;AAAF;;AAGA;EACE,yBAAA;AAAF;;AAGA;EACE;IAAI,SAAA;IACF,0BAAA;EACF;EAAA;IAAK,aAAA;IAAe,0BAAA;EAIpB;EAHA;IAAK,mCAAA;EAML;EALA;IAAK,kCAAA;EAQL;EAPA;IAAK,mCAAA;EAUL;EATA;IAAM,0BAAA;EAYN;AACF;AAVA;EACE;IAAK,2CAAA;EAaL;EAZA;IAAM,8CAAA;EAeN;EAdA;IAAM,4CAAA;EAiBN;EAhBA;IAAM,2CAAA;EAmBN;EAlBA;IAAM,4CAAA;EAqBN;EApBA;IAAM,6CAAA;EAuBN;EAtBA;IAAM,4CAAA;EAyBN;EAxBA;IAAM,4CAAA;EA2BN;EA1BA;IAAM,6CAAA;EA6BN;EA5BA;IAAM,2CAAA;EA+BN;EA9BA;IAAO,6CAAA;EAiCP;AACF","sourcesContent":[".button {\r\n  background-color: transparent;\r\n  border: 1px solid grey;\r\n  border-radius: 0.7rem;\r\n  display: flex;\r\n  justify-content: flex-start;\r\n  align-items: center;\r\n  height: 4rem;\r\n  width: 4rem;\r\n  transition: all 0.4s ease-in-out;\r\n  overflow: hidden;\r\n}\r\n\r\n.button:hover {\r\n  width: 13rem;\r\n  box-shadow: 1px 1px 0 0 grey;\r\n}\r\n\r\n.button:active {\r\n  box-shadow: 0 0 0 0 grey;\r\n  transition: all 0.1s ease-in-out;\r\n  transform: translate(2px, 2px);\r\n}\r\n\r\n.button__text-wrapper {\r\n  overflow: hidden;\r\n  opacity: 0;\r\n  transition: all 0.4s ease-in-out;\r\n}\r\n\r\n.button__text {\r\n  color: grey;\r\n  transition: all 1s ease-in-out;\r\n}\r\n\r\n.button:hover .button__text-wrapper {\r\n  opacity: 1;\r\n  overflow: visible;\r\n}\r\n\r\n.button__image-wrappe